<template>
  <div class="album py-5 bg-light">
    <div class="container">
      <UserProfileCard
        :userProfile="userProfile"
        :is-current-user="currentUser.id === userProfile.id"
        @after-delete-follow="afterDeleteFollow"
        @after-add-follow="afterAddFollow"
      />

      <div class="row">
        <div class="col-md-4">
          <UserFollowingsCard :userFollowings="userFollowings" />
          <br />
          <UserFollowersCard :userFollowers="userFollowers" />
        </div>
        <div class="col-md-8">
          <UserCommentsCard :userComments="userComments" />
          <br />
          <UserFavoritedRestaurantsCard :userFavoritedRestaurants="userFavoritedRestaurants" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import UserProfileCard from "../components/UserProfileCard";
import UserFollowingsCard from "../components/UserFollowingsCard";
import UserFollowersCard from "../components/UserFollowersCard";
import UserCommentsCard from "../components/UserCommentsCard";
import UserFavoritedRestaurantsCard from "../components/UserFavoritedRestaurantsCard";
import { mapState } from "vuex";
import usersAPI from "../apis/users";
import { Toast } from "../utils/helpers";

export default {
  components: {
    UserProfileCard,
    UserFollowingsCard,
    UserFollowersCard,
    UserCommentsCard,
    UserFavoritedRestaurantsCard
  },
  data() {
    return {
      userProfile: {
        id: -1,
        name: "",
        email: "",
        password: "",
        isAdmin: false,
        image: "",
        isFollowed: false,
        userCommentsCount: 0,
        userFavoritedRestaurantsCount: 0,
        userFollowersCount: 0,
        userFollowingsCount: 0
      },
      userComments: [],
      userFavoritedRestaurants: [],
      userFollowers: [],
      userFollowings: []
    };
  },
  computed: {
    ...mapState(["currentUser"])
  },
  methods: {
    async fetchUser(userId) {
      try {
        const { data } = await usersAPI.get({ userId });

        const { profile, isFollowed } = data;
        const {
          id,
          name,
          email,
          password,
          isAdmin,
          image,
          Comments,
          FavoritedRestaurants,
          Followers,
          Followings
        } = profile;
        this.userProfile = {
          id,
          name,
          email,
          password,
          isAdmin,
          image,
          isFollowed,
          userCommentsCount: Comments ? Comments.length : 0,
          userFavoritedRestaurantsCount: FavoritedRestaurants
            ? FavoritedRestaurants.length
            : 0,
          userFollowersCount: Followers ? Followers.length : 0,
          userFollowingsCount: Followings ? Followings.length : 0
        };
        this.userComments = Comments;
        this.userFavoritedRestaurants = FavoritedRestaurants;
        this.userFollowers = Followers;
        this.userFollowings = Followings;
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: error
        });
      }
    },
    afterDeleteFollow() {
      this.userProfile.isFollowed = false;
    },
    afterAddFollow() {
      this.userProfile.isFollowed = true;
    }
  },
  created() {
    const { id: userId } = this.$route.params;
    this.fetchUser(userId);
  },
  beforeRouteUpdate(to, from, next) {
    const { id: userId } = to.params;
    this.fetchUser(userId);
    next();
  }
};
</script>