<template>
  <div class="card mb-3">
    <div class="row no-gutters">
      <div class="col-md-4">
        <img :src="userProfile.image" width="300px" height="300px" />
      </div>
      <div class="col-md-8">
        <div class="card-body">
          <h5 class="card-title">{{userProfile.name}}</h5>
          <p class="card-text">{{userProfile.email}}</p>
          <ul class="list-unstyled list-inline">
            <li>
              <strong>{{userProfile.userCommentsCount}}</strong> 已評論餐廳
            </li>
            <li>
              <strong>{{userProfile.userFavoritedRestaurantsCount}}</strong> 收藏的餐廳
            </li>
            <li>
              <strong>{{userProfile.userFollowingsCount}}</strong> followings (追蹤者)
            </li>
            <li>
              <strong>{{userProfile.userFollowersCount}}</strong> followers (追隨者)
            </li>
          </ul>

          <p>
            <template v-if="isCurrentUser">
              <router-link
                :to="{name:'user-edit',params:{id:userProfile.id}}"
                class="btn btn-primary"
              >Edit</router-link>
            </template>
            <template v-else>
              <button
                type="button"
                v-if="userProfile.isFollowed"
                @click.prevent.stop="deleteFollow(userProfile.id)"
                class="btn btn-danger"
              >取消追蹤</button>

              <button
                type="button"
                v-else
                @click.prevent.stop="addFollow(userProfile.id)"
                class="btn btn-primary"
              >追蹤</button>
            </template>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import usersAPI from "../apis/users";
import { Toast } from "../utils/helpers";
export default {
  props: {
    userProfile: {
      type: Object,
      required: true
    },
    isCurrentUser: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    async addFollow(userId) {
      try {
        const { data } = await usersAPI.addFollow({ userId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.$emit("after-add-follow", userId);
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法追蹤使用者，請稍後再試"
        });
      }
    },
    async deleteFollow(userId) {
      try {
        const { data } = await usersAPI.deleteFollow({ userId });
        if (data.status !== "success") {
          throw new Error(data.message);
        }
        this.$emit("after-delete-follow", userId);
      } catch (error) {
        Toast.fire({
          icon: "error",
          title: "無法取消追蹤使用者，請稍後再試"
        });
      }
    }
  }
};
</script>